'use strict';
import { glTFLoader } from "./glTFLoader";


const av = Autodesk.Viewing;
const namespace = AutodeskNamespace('Autodesk.Viewing.Extensions.glTF');

/**
 * Extension description
 *
 * The glTF extension lets you view glTF 2.0 models in the Viewer. This extension lets you have efficient glTF 2.0 models in your application.
 * 
 * The extension id is: 'Autodesk.glTF'
 *
 * @example
 *   // Use this viewer option to load the glTF extension
 *   var options = {
 *   env: 'AutodeskProduction',
 *   api: 'derivativeV2',  
 *   documentId: 'tests/unittest/models/gltf/duck.gltf'
 *  }
 *
 * @memberof Autodesk.Viewing.Extensions
 * @alias Autodesk.Viewing.Extensions.glTF
 * @see {@link Autodesk.Viewing.Extension} for common inherited methods.
 * @class
 */
export class glTFExtension extends av.Extension {
    /**
     * Autodesk.glTFExtension constructor
     * @constructor
     * @param {Autodesk.Viewing.Viewer3D} viewer - Viewer instance.
     * @param {Object} [options] Options for the glTFExtension
     * @param {boolean} [options.someOption=false] This is how options should be documented
     */
    constructor(viewer, options) {
        super(viewer, options);
        this.modes = [];
        this.mode = '';
        this.name = 'glTF';

        // Use the following functions to add or remove event listeners on the document or window
        // this.addWindowEventListener
        // this.removeWindowEventListener
        // this.addDocumentEventListener
        // this.removeDocumentEventListener
    }

    /**
     * Called by the viewer once for the lifetime of the extension (unload() ends it)
     * Perform all initializations here.
     * The viewer sends `EXTENSION_LOADED_EVENT` after the load succeeds.
     * @returns {boolean} True if the load was successful. Optionally, the function can return a Promise to indicate success (resolve) or failure (reject).
     * @memberof Autodesk.Viewing.Extensions.glTF
     * @alias Autodesk.Viewing.Extensions.glTF#deactivate
     * @private
     */
    /*async*/ load() {
        super.load();

        av.FileLoaderManager.registerFileLoader("gltf", ["gltf","glb"], glTFLoader);
        //This function should not create any UI. That should be done inside of the onToolbarCreated method.
        console.log(`Loaded ${this.name} extension!`);
        return true;
    }

    /**
     * Override the unload method to perform some cleanup of operations that were done in load.
     * This function will be invoked when viewer.unloadExtension("Autodesk.glTF") is called.
     * @returns {boolean} True if the load was successful. Optionally, the function can return a Promise to indicate success (resolve) or failure (reject).
     * @memberof Autodesk.Viewing.Extensions.glTF
     * @alias Autodesk.Viewing.Extensions.glTF#deactivate
     * @private
     */
    unload() {
        super.unload();
        av.FileLoaderManager.unregisterFileLoader("gltf");
        return true;
    }

    /**
     * Override the activate method to enable the functionality of the extension.
     * @param [mode] - An optional mode that indicates a different way the extension can function.
     * @see {@link Autodesk.Viewing.Extension#getModes }
     * @returns {boolean} True if the extension activation was successful.
     * @alias Autodesk.Viewing.Extensions.glTF#activate
     */
    activate(mode) {
        super.activate(mode);
        return true;
    }

    /**
     * Override the deactivate method to disable the functionality of the extension.
     * @returns {boolean} True if the extension deactivation was successful.
     * @alias Autodesk.Viewing.Extensions.glTF#deactivate
     */
    deactivate() {
        super.deactivate();
        return true;
    }

    /**
     * Gets the extension state as a plain object. Intended to be called when viewer state is requested.
     * @param {object} viewerState - Object to inject extension values.
     * @alias Autodesk.Viewing.Extensions.glTF#getState
     * @private
     */
    getState(viewerState) {
        super.getState(viewerState);
    }

    /**
     * Restores the extension state from a given object.
     * @param {object} viewerState - Viewer state.
     * @param {boolean} immediate - Whether the new view is applied with (true) or without transition (false).
     * @returns {boolean} True if restore operation was successful.
     * @alias Autodesk.Viewing.Extensions.glTF#restoreState
     * @private
     */
    restoreState(viewerState, immediate) {
        super.restoreState(viewerState, immediate);
        return true;
    }

    /**
     * Use this method to add new tools to the toolbar.
     * The method is called after viewer's {@link TOOLBAR_CREATED_EVENT} is fired or immediately if the Toolbar is already loaded in the viewer.
     *
     * Must be overriden by subclasses.
     *
     * @param {Autodesk.Viewing.UI.ToolBar} toolbar - toolbar instance.
     *
     * @alias Autodesk.Viewing.Extensions.glTF#onToolbarCreated
     * @private
     */
    onToolbarCreated(toolbar) {
        super.onToolbarCreated(toolbar);
        // Content for the GUI viewer
    }
}
namespace.glTFExtension = glTFExtension;

av.theExtensionManager.registerExtension('Autodesk.glTF', glTFExtension);
